@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace&display=swap');
.header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}

.nav {
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;    
}

a.nav-logo{
    font-family: 'Bruno Ace', cursive;
    color: var(--title-color);
    font-size: var(--h3-font-size);
}


.nav_toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav_list{
    display: flex;
    column-gap: 2.5rem;
}

.nav_link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;
}

.nav_icons,.nav_toggle,
.nav_close{
    display: none;
}

.active-link,
.nav_link:hover{
    color: var(--title-color-dark);
}



/* For medium devices */
@media screen and (max-width: 768px) {
    .header {
        top: initial;
        bottom: 0;
    }

    .nav {
        height: var(--header-height);
    }

    .nav_menu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius:  1.5rem 1.5rem 0 0;
        transition: .3s;
    }

    /*show menu*/
    .show_menu{
        bottom: 0;
    }

    .nav_list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .nav_icons{
        font-size: 1.2rem;
    }

    .nav_close{
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--title-color);
    }

    .nav_close:hover{
        color: var(--title-color-dark);
    }

    .nav_toggle{
        font-size: 1.1rem;
        cursor: pointer;
    }

    .nav_icons,
    .nav_close,
    .nav_toggle{
        display: block;
    }
}

/* For small devices */
@media screen and (max-width: 350px) {
    .nav_menu{
        padding: 2rem 0.25rem 4rem;
    }

    .nav_link{
        column-gap: 0;
    }
}
  