.skills_container {
    grid-template-columns: repeat(1, 350px);   /* if Backend skill want to add then */
    column-gap: 3rem;
    justify-content: center;
}

.skills_content {
    background-color: var(--container-color);
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 2rem 4rem;
    border-radius: 1.2rem;
}

.skills_title {
    font-size: var(--normal-font-size);
    font-weight: va r(--font-medium);
    text-align: center;
    margin-bottom: var(--mb-1-5);
}

.skills_box {
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

.skills_group {
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;
}

.skills_data{
    display: flex;
    column-gap: 0.5rem;
}

.skills .bx-badge-check {
    font-size: 1rem;
    color: var(--title-color);
}

.skills_name{
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    line-height: 18px;
}

.skills_level{
    font-size: var(--tiny-font-size);
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .skills_container{
        grid-template-columns: max-content;
        row-gap: 2rem;
    }
}
  
  /* For medium devices */

@media screen and (max-width: 576px) {
    .skills_container{
        grid-template-columns: 1fr;
    }

    .skills_content{
        padding: 1.5rem;
    }
}
  
  /* For small devices */
  @media screen and (max-width: 350px) {
    .skills_box{
        column-gap: 1.25rem;
    }

    .skills_name{
        font-size: var(--small_font-size);
    }
}
  